<template>
  <div class="content">
    <!-- 树形图 -->
    <el-tree 
      :data="data" 
      default-expand-all
      :props="defaultProps" 
      @node-click="handleNodeClick"></el-tree>
    <div v-for="(node,index) in text" :key="index">
      <!-- 详细内容 -->
      <el-button plain :icon="elicon" class="voicebtn" @click="playVoice(node.text)"></el-button>
      <div v-html="node.text" :id="node.id"></div>
      <!-- 相关题目 -->
      <el-card class="box-card" v-for="(item,i) in node.test" :key="i">
        <div slot="header" class="clearfix">
          {{i+1}}.<span>{{item.title}}</span>
        </div>
        <div v-for="(inlist,l) in item.answer" :key="l" class="text">
          <div @click="answer(index,i,l)" :class="{addbgone:item.thechoice == l}">
            {{inlist}}
          </div>
        </div>
        <div class="footer" v-if="item.isShow"> 
          <p>正确答案:<br>{{item.answer[item.correct]}}</p>
          <p>答案解析:<br>{{item.analysis}}</p>
          <p>相关链接:<br><a :href="item.link">点击跳转</a></p>
        </div>
      </el-card>
    </div>
  </div>
</template>
<script>
const synth = window.speechSynthesis;
const msg = new SpeechSynthesisUtterance();
export default {
  name: 'Content',
  props:{
    data: Array,
    text: Array
  },
  data () {
    return {
      voice: false,
      elicon: 'el-icon-video-play',
      defaultProps: {
        children: 'children',
        label: 'label'
      }
    }
  },
  created () {
    //单选选中值样式
    this.text.forEach(function(item) {
      try {
        item.test.forEach(function (node) {
          node.thechoice = null;
          node.isShow = false;
        })
      } catch (err) {
        console.log(err)
      }
    })
    let thestring = this.text
    thestring.forEach(function (item) {
      // 划重点
      try {
        item.matching.forEach(function (node) {
          item.text = item.text.replace(eval(`/${node.text}/g`), `<span style="${node.style}">${node.text}</span>`)
        })
      } catch (err) {
        console.log(err)
      }
    })
    this.text = thestring
    this.$forceUpdate()
  },
  methods:{
    playVoice(text) {
      // synth.pause();
      //去除html、css
      let thetext = text.replace(/<[^>]+>/g,"")
      if(this.voice === false) {
        this.elicon = 'el-icon-video-pause'
        this.voice = true
        this.handleSpeak(thetext) 
      }else{
        this.elicon = 'el-icon-video-play'
        this.voice = false
        this.handleStop(thetext)
      }
    },
    // 语音播报的函数
    handleSpeak(text) {
      msg.text = text;     
      msg.lang = "zh-CN";
      msg.volume = .8;      // 声音音量：1
      msg.rate = 1;        // 语速：1
      msg.pitch = .8;       // 音高：1
      synth.speak(msg);    // 播放
    },
    // 语音停止
    handleStop(e) {
      msg.text = e;
      msg.lang = "zh-CN";
      synth.cancel(msg);
    },
    handleNodeClick(data) {
      document.querySelector(`#id${data.id}`).scrollIntoView(true);
    },
    answer(index,i,l){
      this.text[index].test[i].thechoice = l;
      this.text[index].test[i].isShow = true;
      this.$forceUpdate()
    }
  }
}
</script>
<style scoped>
  .addbgone{
    color:rgb(255, 255, 255);
    background:#999999;
  }
  .voicebtn{
    margin-top: 25px;
  }
</style>


