<template>
  <div class="search">
    <Header></Header>
    <div class="searchdeiv">
      <el-input class="searchinput" v-model="input" placeholder="输入搜索内容"></el-input>
      <el-button type="primary" @click="search" class="searchbtn">确定</el-button>
    </div>
    <div class="searchtext" v-html="list"></div>
    <div class="previous" @click="previous">上一个</div>
    <div class="next" @click="next">下一个</div>
    <Footer :shownum="shownum"></Footer>
  </div>
</template>
<script>
import Header from '@/components/Header'
import Footer from '@/components/Footer'
import constitution1 from '@/components/constitution1'
import constitution2 from '@/components/constitution2'
import constitution3 from '@/components/constitution3'
import constitution4 from '@/components/constitution4'
export default {  
  components:{
    Header,Footer
  },
  name: 'Search',
  data () {
    return {
      shownum:2,
      list: '',
      input: '',
      activenum: 0,
      total: 0
    }
  },
  created () {
    let _this = this
    constitution1.node.forEach(function (item){
      _this.list += item.text
    })
    constitution2.node.forEach(function (item){
      _this.list += item.text
    })
    constitution3.node.forEach(function (item){
      _this.list += item.text
    })
    constitution4.node.forEach(function (item){
      _this.list += item.text
    })
  },
  methods: {
    search () {
      let _this = this
      let thelist = _this.list
      if (_this.input !== ''){
        let listnum = thelist.match(eval(`/${_this.input}/g`))
        if (listnum) {
          _this.total = listnum.length
          _this.$message({
            message: '匹配到'+listnum.length+'个',
            type: 'success'
          });
          _this.list = thelist.replace(eval(`/${_this.input}/g`), `<span class="matching" style="background:#67c23a;color:#ffffff;">${_this.input}</span>`)
          _this.$forceUpdate()
          setTimeout(function() {
            let matching = document.querySelector('.matching');
            matching.scrollIntoView({block: "end", behavior: "smooth"});
          },200)
        }else{
          _this.$message({
            message: '没有匹配到',
            type: 'warning'
          });
        }
      }else{
        _this.$message({
          message: '不能为空',
          type: 'warning'
        });
      }
    },
    next () {
      if (this.activenum <= this.total) {
        this.activenum++
        let matching = document.querySelectorAll('.matching');
        matching[this.activenum].style.border = '1px solid #CC0000';
        matching[this.activenum].scrollIntoView({block: "end", behavior: "smooth"});
      }
    },
    previous () {
      if(this.activenum !== 0 && this.activenum <= this.total){
        this.activenum--
        let matching = document.querySelectorAll('.matching');
        matching[this.activenum].style.border = '1px solid #000000';
        matching[this.activenum].scrollIntoView({block: "end", behavior: "smooth"});
      }
    }
  }
}
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .search{
    font-size: 12px;
  }
  .searchtext {
    text-align: left;
    padding: 10px;
  }
  .searchinput{
    width:80%;
    height: 40px;
  }
  .searchdeiv{
    position: fixed;
    top: 0;
    width: 100%;
    display: flex;
  }
  .searchbtn{
    width:20%;
    height: 40px;
  }
  .previous{
    width: 50px;
    position: fixed;
    right: 10px;
    top: 15%;
    border: 1px solid #666;
    color: #666;
    border-radius: 5px;
    outline: none;
    background: rgba(255, 255, 255,.8);
  }
  .next{
    width: 50px;
    position: fixed;
    right: 10px;
    top: 20%;
    border: 1px solid #666;
    color: #666;
    border-radius: 5px;
    outline: none;
    background: rgba(255, 255, 255,.8);
  }
</style>


